#title {

  position: absolute;
  left: 0;
  right: 0;
  color: #FFF;
  text-align: center;
  font-family: "lato", sans-serif;
  font-weight: 300;
  font-size: 50px;
  letter-spacing: 10px;
  padding-left: 10px;
  padding-top: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-63%);
}
#title span {
  background: -webkit-linear-gradient(white, #38495a);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@keyframes animStar {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(-2000px);
  }
}
* {
	margin: 0;
	padding: 0;
}
body {
  user-select:none;/* for microsoft edge*/
	display: flex;

	height: 100vh;
	transition: all 0.4s ease-in-out;
	flex-direction: column;
  background: #000000;
}
#title > h1 {
	margin-bottom: 50px;
	font-family: 'Roboto Mono', monospace;
	font-family: 'Varela Round', sans-serif;
	color: #fff;
	position: relative;
	background: linear-gradient(-90deg, #9a52a0, #FFF);
	background-size: 200% auto;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	animation: movingBack 1s cubic-bezier(0.075, 0.1, 0.78, 1) 0.1s backwards,
		changeBack 1s ease-in-out 1.7s;
}
@keyframes movingBack {
	from {
		transform: translate(-200%, 0);
	}
	to {
		transform: translate(0, 0);
	}
}
@keyframes changeBack {
	to {
		background-position: -200% center;
	}
}

